import React from 'react'
import { useState } from 'react';
import DataTableFilterDemo from '../DataTable1';
import DataTableSortDemo from '../DataTable2';
import { Button } from 'primereact/button';



function Paripoorana() {
    const [expand, setExpand] = useState(false);
    const [expand1, setExpand1] = useState(false);

    const toggleExpand = () => setExpand((expand) => !expand);
    const toggleExpand1 = () => setExpand1((expand1) => !expand1);


    return (
        <div className="card shadow mb-4">
            <h2>PARIPOORANA_TASK</h2>
            <h4>TWO SCREENS - SINGLE REUSABLE COMPONENT</h4>
            <h6>CLICK THE BUTTON TO VIEW THE TABLE! THANKYOU</h6>

            <Button label="TABLE 1!" className="p-button-info p-button-rounded mr-3" onClick={toggleExpand} />
            <Button label="TABLE 2!" className="p-button-info p-button-rounded " onClick={toggleExpand1} />


            <div className="card-body">
                {expand && <DataTableFilterDemo />}
                {expand1 && <DataTableSortDemo />}



            </div>
        </div>
    )
}

export default Paripoorana